<template>
    <div class="orderDetail">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>订单详情</span>
        </header>
        <section class="s1">
            <img :src="'https://elm.cangdu.org/img/'+this.order.restaurant_image_url" class="img1">
            <p>等待支付</p>
            <div @click="toShop">再来一单</div>
        </section>
        <section class="s2">
            <div class="s21" @click="toShop">
                <div>
                    <img :src="'https://elm.cangdu.org/img/'+this.order.restaurant_image_url" class="img2">
                    <span>{{order.restaurant_name}}</span>
                </div>
                <i class="el-icon-arrow-right"></i>
            </div>
            <ul>
                <li v-for="a in orderDetail._doc.basket.group[0]">
                    <p>{{a.name}}</p>
                    <div><span>X{{a.quantity}}</span><span>￥{{a.price}}</span></div>
                </li>
            </ul>
            <div class="s23">
                <span>配送费</span>
                <span>{{orderDetail._doc.basket.deliver_fee.price}}</span>
            </div>
            <div class="s22">实付{{order.total_amount}}.00</div>
        </section>
        <section class="s3">
            <div class="s31">配送信息</div>
            <div><p>送达时间：{{orderDetail.deliver_time}}</p></div>
            <div><p>送货地址：</p>
                <div>
                    <p>{{orderDetail.consignee}}</p>
                    <p>{{orderDetail.phone}}</p>
                </div>
            </div>
            <div><p>配送方式：蜂鸟专送</p></div>
        </section>
        <section class="s3">
            <div class="s31">订单信息</div>
            <div><p>订单号：{{order.id}}</p></div>
            <div><p>支付方式：{{orderDetail.pay_method}}</p></div>
            <div><p>下单时间：{{order.formatted_created_at}}</p></div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "OrderDetail",
        data(){
            return{
                user_id:"",
                order:[],
                orderDetail:[],
                msite:[],
                shop:[],
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.order = JSON.parse(localStorage.getItem("order"));
            this.msite = JSON.parse(localStorage.getItem("msite"))
            this.axios.get("/bos/v1/users/"+this.user_id+"/orders/"+this.order.id+"/snapshot").then(res=>{
                this.orderDetail = res;
            }).catch(err=>{})

            this.axios.get("/shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                for(let i = 0; i<res.length;i++){
                    if(res[i].id == this.order.restaurant_id){
                        localStorage.setItem("shop",JSON.stringify(res[i]));
                    }
                }
            }).catch(err=>{});
        },
        methods:{
            toShop(){
                this.$router.push("/shop");
            }
        }
    }
</script>

<style scoped>
    .orderDetail{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: .7rem;
        background-color: #fff;
        margin-bottom: .5rem;
    }
    .img1{
        border: .05rem solid #3190e8;
        border-radius: 50%;
        width: 3rem;
        height: 3rem;
    }
    .s1>p{
        font-size: .9rem;
        color: #333;
        font-weight: 700;
        margin: .4rem 0 0.3rem;
    }
    .s1>div{
        font-size: .6rem;
        color: #3190e8;
        margin-top: .5rem;
        border: .025rem solid #3190e8;
        padding: .25rem .45rem;
        border-radius: .1rem;
    }
    .s2{
        background-color: #fff;
    }
    .s21{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .2rem .5rem;
        border-bottom: 1px solid #f5f5f5;
    }
    .img2{
        width: 1.2rem;
        height: 1.2rem;
        vertical-align: middle;
        margin-right: .2rem;
    }
    .s21>div>span{
        font-size: .75rem;
        color: #333;
        font-weight: 700;
    }
    .s21 i{
        font-size: 0.75rem;
        color:#666;
    }
    .s22{
        font-size: .6rem;
        color: #fb6b23;
        border-top: 1px solid #f5f5f5;
        font-weight: 700;
        line-height: 2rem;
        text-align: right;
        padding-right: .5rem;
    }
    .s2 ul li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .5rem;
        line-height: 2rem;
    }
    .s2 li p{
        font-size: .6rem;
        color: #666;
        flex: 4;
    }
    .s2 li div{
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .s2 li div span:nth-child(1){
        font-size: .6rem;
        color: #ccc;
    }
    .s2 li div span:nth-child(2){
        font-size: .6rem;
        color: #666;
    }
    .s23{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .5rem;
        line-height: 2rem;
        border-top: 1px solid #f5f5f5;
    }
    .s23 span{
        font-size: .6rem;
        color: #666;
    }
    .s3{
        background-color: #fff;
        margin-top: .5rem;
    }
    .s31{
        font-size: .75rem;
        color: #333;
        padding: .5rem;
        border-bottom: 1px solid #f5f5f5;
    }
    .s3>div{
        display: flex;
        padding: .5rem;
    }
    .s3>div>p{
        font-size: .65rem;
        color: #666;
        line-height: 1rem;
    }
    .s3>div>div>p{
        font-size: .65rem;
        color: #666;
        line-height: 1rem;
    }
</style>